import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { format, differenceInCalendarYears, differenceInCalendarMonths } from 'date-fns';
import { Link, Chip } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  card: {
    display: 'flex',
    marginBottom: theme.spacing(2),
  },
  cardMedia: {
    width: 'inherit',
  },
  details: {
    display: 'flex',
    flexDirection: 'row',
  },
  description: {
    display: 'flex',
    flexDirection: 'column',
    minWidth: '350px',
  },
  content: {
    flex: '1 0 auto',
    flexShrink: 'inherit',
    // maxWidth: "400px"
  },
  actions: {
    flex: '0 0 auto',
  },
  contentRoot: { margin: theme.spacing(2, 0) },
  technologieChip: {
    margin: theme.spacing(0.5, 0.5, 0, 0),
  },
}));

const ExperienceContent = ({ title, description, technologies }) => {
  const classes = useStyles();
  return (
    <>
      <Typography gutterBottom variant="overline" component="h1">
        {title}
      </Typography>
      <Typography gutterBottom variant="body1" component="p">
        {description}
      </Typography>
      {technologies.map((technologie, i) => (
        <Chip
          className={classes.technologieChip}
          // eslint-disable-next-line react/no-array-index-key
          key={i}
          label={technologie}
          variant="outlined"
          color="secondary"
          size="small"
        />
      ))}
      <div className={classes.contentRoot}> </div>
    </>
  );
};

const ExperienceCard = ({
  positionTitle,
  companyName,
  companyUrl,
  companyPositionUrl,
  cardImage,
  fromDate,
  toDate,
  experiences,
}) => {
  const classes = useStyles();

  return (
    <>
      <Card className={classes.card}>
        <CardMedia
          classes={{ media: classes.cardMedia }}
          image={cardImage}
          component="img"
          height={250}
          width={250}
          alt="Company logo"
          title="Company logo"
        />
        <div className={classes.details}>
          <div className={classes.description}>
            <CardContent className={classes.content}>
              <Typography gutterBottom variant="h6" component="h1">
                {positionTitle}
              </Typography>
              <Typography gutterBottom variant="h5" component="h2">
                {companyName}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="h3">
                <Link target="_blank" href={companyPositionUrl} color="inherit">
                  Ticino, Switzerland
                </Link>
              </Typography>
              <Typography variant="body2" color="textSecondary" component="h4">
                {format(fromDate, 'MMM YYYY')} - {toDate ? format(toDate, 'MMM YYYY') : 'Present'} Â·{' '}
                {differenceInCalendarYears(toDate || new Date(), fromDate)} yrs{' '}
                {differenceInCalendarMonths(toDate || new Date(), fromDate) % 12} mos
              </Typography>
            </CardContent>
            <CardActions className={classes.actions}>
              <Button size="small" color="primary" target="_blank" href={companyUrl}>
                Company web site
              </Button>
              <Button size="small" color="primary">
                Learn More
              </Button>
            </CardActions>
          </div>
          <CardContent className={classes.content}>
            {experiences.map((experience, i) => (
              <ExperienceContent
                // eslint-disable-next-line react/no-array-index-key
                key={i}
                title={experience.title}
                description={experience.description}
                technologies={experience.technologies}
              />
            ))}
          </CardContent>
        </div>
      </Card>
    </>
  );
};

ExperienceCard.defaultProps = {
  toDate: undefined,
  experiences: [],
};

ExperienceCard.propTypes = {
  positionTitle: PropTypes.string.isRequired,
  companyName: PropTypes.string.isRequired,
  companyPositionUrl: PropTypes.string.isRequired,
  fromDate: PropTypes.instanceOf(Date).isRequired,
  toDate: PropTypes.instanceOf(Date),
  experiences: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
      technologies: PropTypes.arrayOf(PropTypes.string).isRequired,
    })
  ),
};

export default ExperienceCard;
