import React, { useEffect } from 'react';
import { AppBar, Toolbar, Typography, makeStyles, Avatar, Tabs, Tab } from '@material-ui/core';
import MeImage from '../public/me.jpg';
import RouterLink from './RouterLink';
import routerLinks from '../routes/routerLinks';

const useStyle = makeStyles({
  grow: { flexGrow: 1 },
  appBar: {
    position: 'sticky',
  },
});

const PageHeader = React.memo(({ location: { pathname } }) => {
  const classes = useStyle();
  const [value, setValue] = React.useState(0);

  function handleChange(event, newValue) {
    setValue(newValue);
  }

  useEffect(() => {
    setValue(routerLinks.findIndex(routerLink => routerLink.to.startsWith(pathname)));
  }, [pathname]);

  return (
    <AppBar className={classes.appBar}>
      <Toolbar>
        <Typography className={classes.title} variant="h5" noWrap>
          David Nussio{' '}
          <Typography className={classes.title} variant="caption">
            born in 1978, Lugano{' '}
            <span role="img" aria-label="Swiss Flag">
              ðŸ‡¨ðŸ‡­
            </span>
          </Typography>
        </Typography>
        <div className={classes.grow} />
        <div className={classes.sectionDesktop}>
          <Avatar alt="David Nussio" src={MeImage} className={classes.avatar} />
        </div>
      </Toolbar>
      <Tabs variant="fullWidth" value={value} onChange={handleChange}>
        {routerLinks.map(({ component, ...props }, i) => (
          // eslint-disable-next-line react/no-array-index-key
          <Tab key={i} component={RouterLink} {...props} />
        ))}
      </Tabs>
    </AppBar>
  );
});

export default PageHeader;
