import React, { useState, useEffect, useRef } from 'react';
import { CardMedia, Card, CardContent, makeStyles, Typography } from '@material-ui/core';
import Sticky from 'react-stickynode';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  media: {
    boxShadow: theme.shadows[10],
    height: props => {
      return props.height;
    },
  },
}));

const useScroll = () => {
  const [state, setState] = useState({
    x: 0,
    y: 0,
  });
  const onScroll = () => {
    setState({ y: window.scrollY, x: window.scrollX });
  };
  useEffect(() => {
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);
  return state;
};

const CardMediaScrollAware = ({ scroll, ...props }) => {
  const ref = useRef({ offsetTop: 0 });

  // TODO: find out a smart way to calculate height

  const classes = useStyles({ height: Math.max(400, 600 - scroll.y) });
  return <CardMedia ref={ref} className={classes.media} {...props} />;
};

const AboutMe = () => {
  const classes = useStyles();
  const scroll = useScroll();

  return (
    <Card className={classes.root}>
      <Sticky enabled top={20}>
        <CardMediaScrollAware
          scroll={scroll}
          component="img"
          src="/big-running.jpg"
          alt="running on the cloud"
        />
      </Sticky>

      <CardContent>
        <Typography gutterBottom variant="h5" component="h5">
          Hello, my name is David Nussio
        </Typography>
        <Typography variant="body1" component="p">
          I am working as Senior Software Engineer and this my working in progress porfolio. Stay
          tuned!
          <Typography variant="caption" component="div">
            2019/06/28
          </Typography>
        </Typography>
      </CardContent>
    </Card>
  );
};

export default AboutMe;
