import { hot } from 'react-hot-loader/root';
import React, { useEffect, Suspense } from 'react';
import { Route, Switch, withRouter } from 'react-router-dom';
import { Container } from '@material-ui/core';
import routerLinks from './routerLinks';
import PageHeader from '../components/PageHeader';
import { PageNotFound } from '../pages';
import PageContainer from '../components/PageContainer';

const ScrollToTop = withRouter(({ children, location: { pathname } }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return children;
});

function LazyComponent(Component) {
  return props => (
    <Suspense fallback={<div />}>
      <Component {...props} />
    </Suspense>
  );
}

function Main() {
  return (
    <Suspense fallback={<div />}>
      <Container>
        <Route path="/" component={PageHeader} />
        <PageContainer>
          <ScrollToTop>
            <Switch>
              {routerLinks.map(({ to, component }) => (
                <Route exact path={to} component={LazyComponent(component)} />
              ))}
              {/* <Route exact path="/" component={LazyComponent(HomePage)} />
            <Route exact path="/experience" component={LazyComponent(Experience)} />
            <Route exact path="/education" component={LazyComponent(Education)} />
            <Route exact path="/projects" component={LazyComponent(Projects)} /> */}
              <Route component={PageNotFound} />
            </Switch>
          </ScrollToTop>
        </PageContainer>
      </Container>
    </Suspense>
  );
}

export default hot(Main);
